*{box-sizing: border-box;padding:0;margin:0;font-family: 'Josefin Sans', sans-serif;}
body{background: #f8f5f1;}
.header{background: #f8a488;height:auto;padding:10px 5px;color:#fff;text-shadow:1px 1px #333;}
.header h2{font-size: 22px;}
footer{text-align:center;background: #5aa897;padding: 15px 0;color:#fff;font-weight: bold;position: fixed;bottom: 0;width: 100%;}
.App{display: flex;width: 100%;flex-direction: column;height:100%;}
.main_note form{position:relative;width: 320px;padding:10px;min-height:100px;margin:10px auto;background: #fff;box-shadow: 0 0 3px rgba(0,0,0,.1);border-radius: 10px;padding-bottom: 30px;}
input{height:30px;border-radius: 2px;margin-bottom: 5px;}
input, textarea{width:100%;border:1px solid #d3d3d3;padding: 5px}
.plus_sign{min-height:30px !important;min-width: 40px !important;border-radius: 50% !important;background: #fff !important;box-shadow: 0 0 8px rgba(0,0,0,.1);font-size: 2rem;position: absolute !important;bottom:-20px !important ;right: 0;}
textarea{height: 60px;border-radius: 2px}
.contentDiv{width: 100%;padding: 0 5px;box-sizing:border-box;}
.note{width: 250px;height: auto;background: #fff;border-radius: 5px;padding:10px 10px 5px 10px;margin: 0 5px;float:left;box-shadow: 0 0 3px rgba(0,0,0,.1);}
.note h1{font-size: 26px;margin-bottom: 5px;}
.note button{background: transparent;border:0;float: right;}
.note button:focus{outline: 0}
.note button svg{fill:#f8a488;}

@media (max-width:640px){
 .contentDiv{margin-top: 20px;}
.note{width: 97%;margin-bottom:10px;}
}

